import { Component, OnInit, ViewChild } from '@angular/core';
import { LoraSetDeviceProtocolService } from './lora-set-device-protocol.service';
import { AlertService, gettext } from '@c8y/ngx-components';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';
import { Router } from '@angular/router';
import { pipe } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'set-device-protocol',
  templateUrl: './lora-set-device-protocol.component.html'
})

export class LoraAssignDeviceProtocolComponent implements OnInit {
  loading: boolean;
  device: IManagedObject;
  currentProtocol: IManagedObject;
  availableProtocols: IResultList<IManagedObject>;
  newProtocol: IManagedObject;

  filterCurrentProtocol = pipe(
    map((protocols: IManagedObject[]) =>
      protocols.filter(protocol => !this.currentProtocol || this.currentProtocol.id !== protocol.id)
    )
  );
  @ViewChild('loraSetDeviceProtocolForm', { static: false }) loraSetDeviceProtocolForm: any;

  constructor(
    private loraService: LoraSetDeviceProtocolService,
    private alertService: AlertService,
    private router: Router,
    private inventoryService: InventoryService
  ) { }
  async ngOnInit() {
    await this.reload();
  }

  async reload() {
    this.loading = true;
    this.newProtocol = null;
    try {
      await this.loadDevice();
      this.availableProtocols = await this.loraService.getAvailableProtocols();
      this.currentProtocol = await this.loraService.getCurrentProtocol(this.device);
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    } finally {
      this.loading = false;
    }
  }

  async loadDevice() {
    const deviceId = this.router.routerState.snapshot.url.match(/\d+/)[0];
    const { data } = await this.inventoryService.detail(deviceId);
    this.device = data;
  }

  async apply(selectedProtocol) {
    try {
      await this.loraService.applyProtocol(this.device, selectedProtocol);
      await this.reload();
      this.alertService.success(gettext('Device protocol set.'));
      this.loraSetDeviceProtocolForm.reset('dirty');
    } catch (ex) {
      this.alertService.danger(gettext('Could not set device protocol.'));
    }
  }
}
