import { NgModule } from '@angular/core';
import {
  CoreModule,
  FormsModule,
  gettext,
  HOOK_ONCE_ROUTE,
  Route,
  ViewContext
} from '@c8y/ngx-components';
import { LoraAssignDeviceProtocolComponent } from './lora-set-device-protocol.component';
import { LoraAgentGuard } from './lora-agent.guard';
import { LoraSetDeviceProtocolService } from './lora-set-device-protocol.service';

const routes: Route[] = [
  {
    context: ViewContext.Device,
    path: 'assign-protocol',
    component: LoraAssignDeviceProtocolComponent,
    label: gettext('LPWAN'),
    icon: 'c8y-device-protocols',
    canActivate: [LoraAgentGuard]
  }
];

@NgModule({
  declarations: [
    LoraAssignDeviceProtocolComponent
  ],
  imports: [
    CoreModule,
    FormsModule
  ],
  entryComponents: [LoraAssignDeviceProtocolComponent],
  providers: [
    LoraAgentGuard,
    LoraSetDeviceProtocolService,
    {
      provide: HOOK_ONCE_ROUTE,
      useValue: routes,
      multi: true
    }
  ]
})
export class LoraProtocolModule { }
